.tableMainContainer{
    display: flex;
    flex-direction: column;
    overflow:-moz-hidden-unscrollable;
}
.butoonWrapper{
    width: 500px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.textinput{
    width: 270px;
    height:30px;
    padding-left: 10px;
}
.tableContainer{   
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.wraptable{
    width:100%;
    height: 100%;
    margin-top: 5px;
    overflow: scroll;
}
.wraptable ::-webkit-scrollbar{
    display: none;
}
.table{
    width: 120px;
    height: 40px;
    border-bottom: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.table1{
    width: 120px;
    height: 40px;
    border: none;
    border-bottom: 1px solid #9ac1f5;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.table1 p{ 
    text-align: center; 
    font-size: 10px;
    font-weight: 400;
    
}
.table p{
    text-align: center;
}
.table1 img {
    text-align: center;
    width: 90px;
    height: 25px;
}
